import React from "react";
import { render } from "react-dom";

import { ThemeProvider } from "styled-components";
import Header from "./components/Header";

export const theme = {
  colors: {
    primary: "#B8222D",
    primaryText: "#000",
  },
};

const App = () => {
  return (
    <ThemeProvider theme={theme}>
      <Header />
    </ThemeProvider>
  );
};

render(<App />, document.getElementById("root"));
